          outline: none;
        }
      }
      > div:nth-child(2) {
        width: 350px;
        height: 40px;
         //background-color: red;
         display: flex;
         justify-content: space-evenly;
         align-items: center;
         select {
          border-radius: 5px;
          height: 30px;
