<template>
  <!-- /* eslint-disable */ -->
  <div class="page category">
    <div class="title--noFilterSearch">
      <div>
        <div class="contTitle">
          <div>
            <img
              src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRfifgpU6f6DdemdITBpg_essXRVuwWFaTnhJgl9QGDvchprPgRpZFDLgAZhDRlBiYvEd8&usqp=CAU"
            />
          </div>
          <p :class=" { 'dark-mode-title': getDarkMode } ">category grid</p>
        </div>
        <div>
          <div class="export">
            <img
              src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRmvEXj1Sr-tzeKuEP9PgzajIUDvR_-L-zfkg&usqp=CAU"
            />
            <button>export</button>
          </div>
          <router-link to="/dashboard/addcatagory">
            <button class="add">+ add catagory</button>
          </router-link>
        </div>
      </div>
      <div :class="{ 'dark-mode-box': getDarkMode }">
        <input
          :class="{ 'dark-mode-search': getDarkMode }"
          placeholder="search"
          type="search"
          v-model="searchQuery"

        />
        <div :class="{ 'dark-mode-box': getDarkMode }">
          <div>
            <button>category</button>
            <img
              class="arroow point"
              src="data:image/png;base64,"
            />
          </div>
          <div>
            <button>last aded</button>
            <img
              class="arroow aded"
              src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcS_HHn9uGD0sPLHcGzdjI_WX8Au22ap-x1b1Q&usqp=CAU"
            />
          </div>
        </div>
      </div>
    </div>
    <div :class="{ 'dark-mode-box': getDarkMode }" class="allContent">

      <div v-if="isLoading">
        <BoxSkeletonLoader v-for="n in 6" :key="n" />
      </div>
      <CategoryList v-else-if="filteredCategories.length > 0" :Categories="filteredCategories"></CategoryList>
      <NoData v-else context="categories"></NoData>

    </div>
   
  </div>
</template>

<script>
import { mapState, mapActions } from 'pinia';
//  store
import { useCategoriesStore } from '@/store/categories/categories.js';
//  CategoryList
import CategoryList from "@/components/categories/CategoryList.vue";
 // NoData
 import NoData from "@/shared/components/noData/NoData.vue";
// Skeleton Box
import BoxSkeletonLoader from '@/shared/components/loading/skeletonLoader/BoxSkeletonLoader.vue';


export default {
  name: "Category",
  components: {
    CategoryList,
    NoData,
    BoxSkeletonLoader,


  },
  data() {
    return {
      searchQuery: '',
      isLoading: true,

    };
  },
  computed: {
    getDarkMode() {
      return this.$store.state.darkMode;
    },
    ...mapState(useCategoriesStore, ['categories']),
    filteredCategories() {
      const query = this.searchQuery.toLowerCase();
      // just to be more clear code and less time (optmization) but this condition not  nessesry event the query is "empty string" no problem will return all categories
      if (!query) {
        return this.categories;
      }
      return this.categories.filter(category =>
        category.name.toLowerCase().includes(query)
      );
    },
  },
  methods: {
    ...mapActions(useCategoriesStore, ['fetchCategories']),
  
  },
 async created() {
  await this.fetchCategories();
  this.isLoading = false;


  },
};
</script>

<style scoped lang="scss">


.allContent > div  {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 100%;
  min-height: 380px;
  // background-color: red;
}

// phone
@media (max-width: 477px) {
  .title--noFilterSearch {
    height: 220px;

    > div:nth-of-type(2) {
      height: 30%;
      input {
        width: 130px;
      }
    }
  }

  
.title--noFilterSearch {

> div:first-child {
  height: 70%;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;

   >div:first-child {
    width: 196px;
    p {
      margin-left: 0px;
      font-size: 16px;
      margin-right: 0px;
    }
  }
  > div:nth-of-type(2) {
    display: flex;
    justify-content: flex-end;
    a{
      margin-left: 20px;
      button {
        width: 120px;
        font-size: 15px;
      }
      
    }
    align-items: flex-end;
    > div {
      width: 70px;
      button {
        width: 40px;
      }
      img {
        width: 16px;
        height: 16px;
        margin-left: 3px;
        margin-right: 3px;
      }
    }
  }
}
> div:nth-of-type(2) {
  margin-left: 4%;

  input {
    margin-left: 4px;

  }

}
}

 
}

</style>
